<template>
  <view class="assistant-container">
    <image class="assistant-gif" src="/static/机器1.gif" mode="aspectFit" />
    <view class="bubble" v-if="greeting">
      <text>{{ greeting }}</text>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      greeting: ''
    }
  },
  onLoad() {
    const userInfo = uni.getStorageSync('userInfo') || {};
    const name = userInfo.realName || userInfo.teacherName || userInfo.nickname || userInfo.username || '';
    const first = name ? name.charAt(0) : '';
    this.greeting = `你好，${first}老师，我是你的智能小助手小言。请问我有什么可以帮助你的？`;
  }
}
</script>

<style scoped>
.assistant-container {
  position: relative;
  min-height: 100vh;
  background: #eaf6ff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.assistant-gif {
  width: 280rpx;
  height: 280rpx;
}
.bubble {
  position: absolute;
  bottom: 20%;
  right: 16%;
  max-width: 70%;
  background: #ffffff;
  color: #333;
  font-size: 28rpx;
  line-height: 1.6;
  padding: 20rpx 24rpx;
  border-radius: 16rpx;
  box-shadow: 0 6rpx 20rpx rgba(0,0,0,0.08);
  border: 1rpx solid #e0f0ff;
}
.bubble::after {
  content: '';
  position: absolute;
  bottom: -16rpx;
  right: 40rpx;
  width: 0;
  height: 0;
  border-left: 16rpx solid transparent;
  border-right: 16rpx solid transparent;
  border-top: 16rpx solid #ffffff;
}
</style>


